body {
  margin: 0;
  padding: 0;
  background-color: aqua;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  padding: 20px 30px;
  background-color: white;
  border-radius: 5px;
}

.input-container {
  padding: 10px;
  height: 30px;
  display: flex;
  border: solid 2px greenyellow;
  border-radius: 5px;
}
.search-city {
  flex: 1;
  width: 100%;
  outline: none;
  border: none;
}
.search-icon {
  width: 20px;
  height: 30px;
  cursor: pointer;
}
.search-icon img {
  height: 30px;
  width: 30px;
}
.image {
  display: flex;
  justify-content: center;
  padding: 10px 20px;
}
.image img {
  padding: 5px;
}
.temp,
.city,
.country {
  margin-top: 5px;
  text-align: center;
  padding: 2px 4px;
  color: rgb(0, 119, 255);
  text-transform: uppercase;
}
.city {
  color: brown;
  font-size: 18px;
  font-weight: 600;
}
.coord {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.lat,
.log {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.text-percentage,
.coord div span:nth-child(2) {
  color: red;
  font-weight: bold;
}
.data-container {
  display: flex;
  justify-content: space-between;
  text-align: center;
  margin-top: 20px;
}
.icon {
  width: 50px;
  height: 50px;
}
.text {
  font-size: 12px;
  color: #888;
}
.city-not-found,
.loading-message,
.error-message {
  margin-top: 10px;
  color: #888;
  font-size: 25px;
  font-weight: 500;
  text-align: center;
}
.copyright {
  margin-top: 10px;
  color: #888;
  font-size: 12px;
  font-weight: 500;
}
.copyright span {
  color: blue;
}
